import { Component, Input, OnInit } from '@angular/core';
import { IOptionsPsContainerHtmlViewerExposed } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsActionIcon } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsComplexTooltipExposed } from './ps-complex-tooltip.component.interface';

@Component({
  selector: 'ps-complex-tooltip',
  templateUrl: './ps-complex-tooltip.component.html',
  styleUrls: ['./ps-complex-tooltip.component.scss'],
})
export class PsComplexTooltipComponent extends PsBaseFieldComponent implements OnInit {

  @Input() public options: PsComplexTooltipExposed = {};
  tooltipOptions: IOptionsPsActionIcon = {
    iconName: 'info'
  };
  showArrow: boolean = true;
  htmlViewerOptions: IOptionsPsContainerHtmlViewerExposed;
  ifResult = false;
  constructor(public commonProv: PsCommonService, public loggerP: LoggerService) {
    super(commonProv, loggerP);
  }

  ngOnInit() {
    this.tooltipOptions.iconName = this.options.iconName ? this.options.iconName : "info";
    const result = this.options.fileName;
    if (result) {
      this.htmlViewerOptions = {
        fileName: result
      };
      this.ifResult = true;
    }

  }

}